.icon-container {
    position: relative;
    display: inline-block;
}


.tooltip {
    visibility: hidden;
    background-color: black;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    position: absolute;
    bottom: 120%;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    font-size: 14px;
    opacity: 0;
    transition: opacity 0.3s;
}

.icon-container:hover .tooltip {
    visibility: visible;
    opacity: 1;
}

/* Default styles (for desktop/web) */
.icon-container {
    font-size: 40px;
    text-align: center;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.about-section h2 {
    text-align: center;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 30px;
    font-family: "monospace";
}




/* For smaller screens (tablets and mobile) */
@media screen and (max-width: 1000px) {
    .icon-container {
        font-size: 30px; /* Reduce icon size */
        flex-wrap: wrap; /* Allow icons to wrap */
        gap: 10px;
    }

    h2 {
        font-size: 18px; /* Reduce heading size */
        text-align: center;
    }

    .tooltip {
        font-size: 12px; /* Adjust tooltip size */
    }

    .about-section {
        display: flex;
        gap: 100px;
        flex-direction: row; /* Stack content */
        text-align: center;
        margin-left: 0;
        padding: 20px;
    }

    .about-section img {
        width: 150px; /* Adjust image size */
        height: 150px;
        margin: auto;
        position: static;
    }

    .about-section div {
        width: 100%;
        margin: auto;
        font-size: 16px;
    }

    .skills-card {
        padding: 10px;
    }
}

/* For very small screens (phones) */
@media screen and (max-width: 480px) {
    .icon-container {
        font-size: 25px;
        flex-direction: column; /* Stack icons */
        align-items: center;
    }

    .tooltip {
        font-size: 10px;
    }

    .about-section img {
        width: 120px;
        height: 120px;
    }

    .about-section div {
        font-size: 14px;
    }

    
}
.about-para {
    text-align: left;
    width: 850px;
    margin-left: 130px;
    overflow-y: scroll;
    margin-top: 50px;
    font-family: monospace;
    font-style: italic;
    font-size: 20px;
}
.about-section {
    display: flex;
    flex-direction: row; /* Stack content */
    text-align: center;
    margin-left: 0;
    padding: 20px;
}
@media screen and (max-width: 1000px) {
    .about-para {
        width: 50%;
        margin-left: 20px;
        margin-right: 300px;
        font-size: 18px;
        overflow-y: auto;
    }
}

.experience-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    justify-content: space-evenly;
}

.flip-card {
    width: 350px;
    height: 350px;
    perspective: 1000px;
}

.flip-card-inner {
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.5s;
}

.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
    position: absolute;
    width: 110%;
    height: 130%;
    backface-visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ddd;
    border-radius: 10px;
}

.flip-card-front {
    display: grid;
        align-content: center;
    background-color: rgba(243, 212, 134, 0.579);
    color: black;
}

.flip-card-back {
    background-color: #e9b56d;
    color: rgb(0, 0, 0);
    transform: rotateY(180deg);
    overflow-x: scroll;
    overflow-y: scroll;
}

.experience-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.experience-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    font-size: 14px;
    background: rgba(255, 255, 255, 0.1); /* Soft transparent background */
    border-radius: 8px;
    transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
}

.experience-item:hover {
    transform: scale(1.05);
    background: rgba(255, 255, 255, 0.2); /* Lighter background on hover */
}

.icon {
    font-size: 18px; /* Adjust icon size */
    color: #FFD700; /* Gold color for icons */
    animation: bounce 1s infinite alternate;
}

/* Add a subtle bounce effect to icons */
@keyframes bounce {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(-3px);
    }
}

